import { post } from "axios";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import {
  Section,
  BoxFlex,
  BoxBlock,
  Hr,
  InputDiv,
  Title,
  Label,
  InputS,
  InputM,
  InputR,
  InputL,
  SendButton,
  Error,
} from "./Form.style";

function Form() {
  const navigate = useNavigate();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    const recruitmentFormRequestDto = {
      companyName: data.companyName,
      managerName: data.managerName,
      managerPhoneNumber: data.managerPhoneNumber,
      managerEmail: data.managerEmail,
      recruitmentPosition: data.recruitmentPosition,
      projectDescription: data.projectDescription,
      recruitmentType: data.recruitmentType,
      roleDescription: data.roleDescription,
      minimumAnnual: data.minimumAnnual,
      essentialSkillStack: data.essentialSkillStack,
      optionalSkillStack: data.optionalSkillStack,
      keywords: data.keywords,
      maximumSalary: data.maximumSalary,
      etcComment: data.etcComment,
    };
    formData(recruitmentFormRequestDto);
  };

  async function formData(data) {
    const url = "/api/enterprise/recruit/form";

    try {
      const response = await post(url, data);
      console.log(response);
      navigate("/api/enterprise/recruit/complete");
    } catch (error) {
      console.error(error);
    }
  }

  return (
    <Section>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Title>      .</Title>
        <div>
          <BoxFlex>
            <InputDiv>
              <Label></Label>
              <InputS
                placeholder="ex)  "
                style={
                  errors.companyName ? { border: "2px solid #ff0000" } : {}
                }
                {...register("companyName", { required: true })}
              />
              {errors.companyName && <Error>  .</Error>}
            </InputDiv>
            <InputDiv>
              <Label> </Label>
              <InputS
                placeholder="ex) "
                style={
                  errors.managerName ? { border: "2px solid #ff0000" } : {}
                }
                {...register("managerName", { required: true })}
              />
              {errors.managerName && (
                <Error>   .</Error>
              )}
            </InputDiv>
          </BoxFlex>
          <BoxFlex>
            <InputDiv>
              <Label> </Label>
              <InputS
                placeholder="ex) 010-1234-5678"
                style={
                  errors.managerPhoneNumber
                    ? { border: "2px solid #ff0000" }
                    : {}
                }
                {...register("managerPhoneNumber", {
                  required: true,
                  pattern: /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/,
                })}
              />
              {errors.managerPhoneNumber &&
                errors.managerPhoneNumber.type === "required" && (
                  <Error>  .</Error>
                )}
              {errors.managerPhoneNumber &&
                errors.managerPhoneNumber.type === "pattern" && (
                  <Error>    . ( X)</Error>
                )}
            </InputDiv>
            <InputDiv>
              <Label></Label>
              <InputS
                placeholder="ex) qwer1234@abc.com"
                style={
                  errors.managerEmail ? { border: "2px solid #ff0000" } : {}
                }
                {...register("managerEmail", {
                  required: true,
                  pattern:
                    /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i,
                })}
              />
              {errors.managerEmail &&
                errors.managerEmail.type === "required" && (
                  <Error>  .</Error>
                )}
              {errors.managerEmail &&
                errors.managerEmail.type === "pattern" && (
                  <Error>    .</Error>
                )}
            </InputDiv>
          </BoxFlex>
        </div>
        <Hr />
        <div>
          <BoxFlex>
            <InputDiv>
              <Label> </Label>
              <InputS
                placeholder="ex) Spring "
                style={
                  errors.recruitmentPosition
                    ? { border: "2px solid #ff0000" }
                    : {}
                }
                {...register("recruitmentPosition", { required: true })}
              />
              {errors.recruitmentPosition && (
                <Error>   .</Error>
              )}
            </InputDiv>
            <InputDiv>
              <Label> </Label>
              <InputS
                placeholder="ex)     "
                style={
                  errors.roleDescription ? { border: "2px solid #ff0000" } : {}
                }
                {...register("roleDescription", { required: true })}
              />
              {errors.roleDescription && (
                <Error>   .</Error>
              )}
            </InputDiv>
          </BoxFlex>
          <BoxFlex>
            <InputDiv>
              <Label> </Label>
              <InputS
                placeholder="ex)   "
                style={
                  errors.projectDescription
                    ? { border: "2px solid #ff0000" }
                    : {}
                }
                {...register("projectDescription", { required: true })}
              />
              {errors.projectDescription && (
                <Error>   .</Error>
              )}
            </InputDiv>
            <InputDiv>
              <Label> </Label>
              <InputS
                placeholder="ex)  or  or "
                style={
                  errors.recruitmentType ? { border: "2px solid #ff0000" } : {}
                }
                {...register("recruitmentType", { required: true })}
              />
              {errors.recruitmentType && (
                <Error>   .</Error>
              )}
            </InputDiv>
          </BoxFlex>
        </div>
        <Hr />
        <div>
          <BoxBlock>
            <Label>  </Label>
            <BoxFlex style={{ marginLeft: "-25px" }}>
              <Label>
                <InputR
                  {...register("minimumAnnual", { required: true })}
                  type="radio"
                  name="minimumAnnual"
                  value="ZEROTOONE"
                />
                0-1
              </Label>
              <Label>
                <InputR
                  {...register("minimumAnnual", { required: true })}
                  type="radio"
                  name="minimumAnnual"
                  value="TWOTOFOUR"
                />
                2-4
              </Label>
              <Label>
                <InputR
                  {...register("minimumAnnual", { required: true })}
                  type="radio"
                  name="minimumAnnual"
                  value="FIVETOSEVEN"
                />
                5-7
              </Label>
              <Label>
                <InputR
                  {...register("minimumAnnual", { required: true })}
                  type="radio"
                  name="minimumAnnual"
                  value="EIGHTTOTEN"
                />
                8-10
              </Label>
              <Label>
                <InputR
                  {...register("minimumAnnual", { required: true })}
                  type="radio"
                  name="minimumAnnual"
                  value="MORETHANTEN"
                />
                10 +
              </Label>
            </BoxFlex>
            <Error>
              {errors.minimumAnnual?.type === "required" &&
                "    ."}
            </Error>
          </BoxBlock>
        </div>
        <Hr />
        <div>
          <BoxBlock>
            <Label>  </Label>
            <InputL
              placeholder="ex) Spring, Spring Boot, JPA, Thymeleaf, Lombok, MySQL"
              style={
                errors.essentialSkillStack
                  ? { border: "2px solid #ff0000" }
                  : {}
              }
              {...register("essentialSkillStack", { required: true })}
            />
            {errors.essentialSkillStack && (
              <Error>    .</Error>
            )}
          </BoxBlock>
          <BoxBlock>
            <Label>  </Label>
            <InputL
              placeholder="ex) AWS EC2, React.JS"
              style={
                errors.optionalSkillStack ? { border: "2px solid #ff0000" } : {}
              }
              {...register("optionalSkillStack", { required: true })}
            />
            {errors.optionalSkillStack && (
              <Error>    .</Error>
            )}
          </BoxBlock>
        </div>
        <Hr />
        <div>
          <BoxFlex>
            <InputDiv>
              <Label>   </Label>
              <InputS
                placeholder="ex) Spring Developer"
                style={errors.keywords ? { border: "2px solid #ff0000" } : {}}
                {...register("keywords", { required: true })}
              />
              {errors.keywords && (
                <Error>     .</Error>
              )}
            </InputDiv>
            <InputDiv>
              <Label> </Label>
              <InputS
                placeholder="ex)  2500"
                style={
                  errors.maximumSalary ? { border: "2px solid #ff0000" } : {}
                }
                {...register("maximumSalary", { required: true })}
              />
              {errors.maximumSalary && (
                <Error>   .</Error>
              )}
            </InputDiv>
          </BoxFlex>
          <BoxBlock>
            <Label> </Label>
            <InputM
              placeholder="ex)   "
              style={errors.etcComment ? { border: "2px solid #ff0000" } : {}}
              {...register("etcComment", { required: true })}
            />
            {errors.etcComment && <Error>   .</Error>}
          </BoxBlock>
        </div>
        <SendButton type="submit" />
      </form>
    </Section>
  );
}
export default Form;
